@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.tooltip {
  position: absolute;
  z-index: 1070;
  display: block;
  margin: 0;
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
  font-style: normal;
  font-weight: 400;
  line-height: 1.25;
  text-align: left;
  text-align: start;
  text-decoration: none;
  text-shadow: none;
  text-transform: none;
  letter-spacing: normal;
  word-break: normal;
  word-spacing: normal;
  white-space: normal;
  line-break: auto;
  font-size: .65rem;
  word-wrap: break-word;
  opacity: 0;
}

.dashed{
  stroke-dasharray: 5,5;
}

.solid{

}

.brushed {
  fill: #ff3399;
  stroke: #8e1b54;
  opacity: 1.0;
}

.non_brushed {
  fill: #404040;
  opacity: 0.5;
}

.venntooltip {
  position: absolute;
  text-align: center;
  width: 128px;
  height: 16px;
  background: #333;
  color: #ddd;
  padding: 2px;
  border: 0px;
  border-radius: 8px;
  opacity: 0;
}

.rect_tooltip {
  width: 100%;
  height: 100%;

  stroke-width: 5px;
  opacity: 0.5
}

.loading_icon{
  margin: auto;
  /*background:#FFFFFF; */
  display: block;
  shape-rendering: auto;
}

.loading_icon_path{
  transform:scale(0.8);transform-origin:50px 50px
}